import React, { useMemo } from 'react'

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Button from '../../../components/Button'
import { responsive } from '../../../components/ThemeProvider/theme'

import useCsvButton from '../../useCsvButton'
import icons from '../../icons'
import { trackClick } from '../../../utils/trackEvent'

const iconBg = {
  facebook: 'custom.fbBlue',
  line: 'custom.lineGreen',
}

const Info = () => {
  const buttons = useCsvButton()
  const socialMedia = useMemo(() => buttons.data?.filter(({ id }) => id?.startsWith('socialmedia')) || [], [buttons])
  return (
    <Box pt="2.5em" textAlign="center">
      <Text.Bold  color="custom.blue" fontSize={responsive('1.125em', '1.5em')}>凱基證券投資信託股份有限公司</Text.Bold>
      <Text fontSize="0.75em">107年金管投信新字第003號</Text>
      <Text mt="1em" color="custom.darkGray" fontSize={responsive('0.875em', '1em')}>104台北市中山區明水路698號</Text>
      <Text color="custom.lightBlue" fontSize={responsive('0.875em', '1em')} textDecoration="underline">(02)2181-5678</Text>
      <Flex justifyContent="center" mt="1em" pb="3em">
        {socialMedia.map(({ name, link, icon }, i) => name && (
          <Button
            bg={iconBg[icon]}
            href={link}
            leftIcon={icon && icons[icon]}
            borderRadius="0"
            _hover={{
              bg: iconBg[icon]
            }}
            onClick={() => trackClick(name)}
            width={responsive('40%', '15%')}
            fontSize={responsive('0.875em', '1.125em')}
            mx="0.25em"
            isExternal
            key={i}
          >
            {name}
          </Button>
        ))}
      </Flex>
    </Box>
  )
}

export default Info
